.list {
    max-width: 50rem;
    margin: auto;
    padding: 2rem;
    color: #fff;
}
.definition {
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 5px;
    background-color: rgba(11, 240, 11, 0.274);
    line-height: 2rem;
}
.def {
    letter-spacing: .15rem;
    font-weight: 700;
}
h2 {
    font-size: 2.3rem;
    color: green;
    text-shadow: 1px 1px 2px #fff;
}
.top {
    display: flex;
    justify-content: center;
    text-align: center;
}
@keyframes anime {
    0% {
        opacity: 0;
        transform: translateY(100%);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}
.animate {
    animation: anime 1s;
}


@media only screen and (max-width: 400px) {
    .header h1 {
        font-size: 2.5rem;
    }
    h2 {
        font-size: 1.5rem;
    }
}